'use client';

import { ChangeEvent, useEffect, useRef, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";

import {
  TableHead,
  TableRow,
  TableCell,
  TableHeader,
  TableBody,
  Table
} from '@/components/ui/table';
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle
} from '@/components/ui/card';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { DateTimePicker } from '@/components/ui/datetimepicker';

import ModalDialog from '@/components/ui/modal-dialog';

import { InvoiceRow } from './invoice';

import { showToast } from '@/lib/utils/toast-helper';

import { 
  resetStatus,
  getAllInvoices,
  selectData,
  selectStatus,
  selectItemList,
  deleteByID,
} from '@/lib/features/document_ocr/document.slice'

import { useAppDispatch, useAppSelector } from "@/lib/hooks/hooks";

export function InvoicesTable({
  currentPageNo,
  offset,
  totalItems
}: {
  currentPageNo: number,
  offset: number;
  totalItems: number;
}) {

  const router = useRouter();

  const dispatch = useAppDispatch();

  const status = useAppSelector(selectStatus);
  const itemList = useAppSelector(selectItemList);

  const initialized = useRef(false)

  const [modal, setModal] = useState(false);
  const [deleteId, setDeleteID] = useState(-1);

  const [formData, setFormData] = useState({
    type: '',
    item: '',
    fromdate: null,
    todate: null,
  });

  let itemPerPage = 10;

  useEffect(() => {
    
    if (!initialized.current) {
      initialized.current = true;
      console.log("EFFECT STATUS >>> ", status);

      if (status === "success") {
        setModal(false);
        showToast("success", "Data has been saved!");
        dispatch(resetStatus());
      }
    }
    
    if(status === "edit") {
      router.push(`/document_ocr`);
    }

    if(status === "deleted") {
      setModal(false);
      showToast("success", "Data has been deleted!");
    }

    if(itemList == null)
      dispatch(getAllInvoices());
    
  }, [status]);

  function prevPage() {
    router.back();
  }

  async function nextPage() {
    
  }

  const handleClose = () => setModal(false);
  const handleShow = () => setModal(true);

  const handleInputChange = (e: any) => {
      const { name, value } = e.target;
      setFormData((prevFormData) => ({
          ...prevFormData,
          [name]: value,
      }));
  };

  const handleShowModalDialog = (id: number) => {
    setModal(true);
    setDeleteID(Number(id));
  }

  const handleProcess = () => {
    console.log("SET DELETE ID >> ", deleteId);
    dispatch(deleteByID({id: Number(deleteId)}));
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Invoices</CardTitle>
        <CardDescription>
          Invoice Listing
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-4 gap-4 mt-4 mb-12">
          <div className="mt-2">
              <div className="flex flex-row items-center">
                  <label className="basis-1/4">From Date :</label>
                  <DateTimePicker size={30} value={formData.fromdate} timeDisplay={false}/>
              </div>
          </div>
          <div className="mt-2">
              <div className="flex flex-row items-center">
                  <label className="basis-1/4">To Date :</label>
                  <DateTimePicker size={30} value={formData.fromdate} timeDisplay={false}/>
              </div>
          </div>
        </div>
        <div className="grid grid-cols-4 gap-4 mt-4 mb-12">
          <div className="mt-2">
              <div className="flex flex-row items-center">
                  <label className="basis-1/4">Item :</label>
                  <Input type="text" 
                      name="item"
                      onChange={handleInputChange}
                      size={30}
                      className="text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
              </div>
          </div>
          <div className="mt-2">
              <div className="flex flex-row items-center">
                  <label className="basis-1/4">Type :</label>
                  <Input type="text" 
                      name="type"
                      onChange={handleInputChange}
                      size={30}
                      className="text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
              </div>
          </div>
          <div>
            <Button type='submit' size="lg" variant="default" className="gap-1">
                <Upload className="h-4 w-4" />
                <span className="sr-only sm:not-sr-only sm:whitespace-nowrap">
                    Save
                </span>
            </Button>
          </div>
        </div>


        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Item</TableHead>
              <TableHead>Vendor</TableHead>
              <TableHead>Place</TableHead>
              <TableHead>Date</TableHead>
              <TableHead>Type</TableHead>
              <TableHead>Amount</TableHead>
              <TableHead>
                <span>Actions</span>
              </TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {itemList?.length ? (
              itemList?.map((invoice: any) => (
              <InvoiceRow key={invoice.id} invoice={invoice} handleShowModalDialog={handleShowModalDialog} />
            ))
            ) : (
              <TableRow>
                <TableCell className="font-medium"><span>No record found</span></TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </CardContent>
      <CardFooter>
        
      </CardFooter>

      <ModalDialog
        show={modal}
        handleClose={handleClose}
        handleProcess={handleProcess}
      />
    </Card>
  );
}
